@import '../../variables.scss';

.map-view {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

iframe{
    border: unset;
}

.map-text{
    font-size: 24px;
    margin: 0;
    padding: 24px 0;
}

@media (max-width: 739px) {
    .map-view{
        height: $map-height-s;
    }
}